* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: url(/CSCI-499-Drip-Me-Out/static/media/closet_image.09b2d6c9.jpg) no-repeat center fixed;
  background-size: cover;
  font-family: 'Poppins', sans-serif;
}

.container {
  /* max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid steelblue;
  padding: 30px;
  border-radius: 5px; */
}

.header {
  background: #444;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.clothing-form {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  padding: 30px;
  border-radius: 5px;
  background: #444;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  /* float: left */
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1 1;
}

.form-control-check input {
  flex: 2 1;
  height: 20px;
}

.clothing-image {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  padding: 30px;
  border-radius: 5px;
  background: #444;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  /* float: right; */
}

#title {
   text-align: center;
   color: white;
   background-color: rgba(0,0, 0, .5);
   width: 60%;
   border-radius: 25px;
}

#title h1{
  font-size: 350%;
}

#login {
  text-align: center;
  background-color: rgba(0,0, 0, .5);
  width: 60%;
  border-radius: 25px;
  margin: auto;
  padding: 25px 50px;
}

#login input{
  border-radius: 25px;
  padding: 10px;
  width: 40%;
}

#login-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 10%;
}

#login-form{
  color: white;
}

#login-buttons{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-top:20px;
}

#navbar {
   height: 60px;
   background-color: rgba(0,0, 0, .5);
   text-align: right;
   padding: 16px;
}

#logout {
   padding: 30px;
   text-decoration: none;
   color: white;
   font-weight: lighter;
}

#logout:hover {
   position: relative;
   bottom: 2.5px;
   cursor: pointer;
}

p {
   color: white;
   padding: 20px;
}

.boxes {
   background-color: rgba(0,0, 0, .5);
   margin-top: 10%;
   float: left;
   text-align: center;
   border-radius: 25px;
   width: 32%;
}

#box1 {
   margin-left: 1%;
}

#box2 {
   margin-left: 1%;
   margin-right: 1%;
}

#box3 {
   margin-right: 1%;
}

.smalltext {
   font-weight: lighter;
   font-size: small;
}

#dripmeout {
   position: absolute;
   top: 70%;
   left: 0;
   right: 0;
   margin-left: auto;
   margin-right: auto;
   width: 400px;
}

h1 {
   font-size: small;
   color: white;
}
